<style scoped>
.reduce-padding {
	padding-bottom: 0.25rem;
}

.small-text {
	font-size: 0.75rem;
}

tr > th:first-child,
tr > td:first-child {
	padding-top: 1.25rem;
	text-align: center;
	width: 4.5rem;
}
tr > td:nth-child(2) > select {
	width: 8.5rem;
}
tr > td:nth-child(3) > select {
	width: 8rem;
}
tr > td:nth-child(4) > div,
tr > td:nth-child(6) > input,
tr > td:nth-child(8) > input {
	width: 5.5rem;
}
tr > td:nth-child(5) > input {
	width: 6rem;
}
tr > td:last-child > select {
	width: 6rem;
}
</style>

<template>
	<tr>
		<td v-text="getDriveCaption(index)"></td>
		<td>
			<b-select v-model.number="drive.direction" v-preset="presetDrive.direction" title="Direction of this drive">
				<option value="0">Backwards</option>
				<option value="1">Forwards</option>
			</b-select>
		</td>
		<td>
			<b-select v-model="driverOption" v-preset="template.lpc.stepperDriver" title="Stepper Driver" :disabled="board.lpc.hasBuiltinDrivers">
				<option v-for="(value, name) in stepperDriverTimings" v-bind:value="name">{{name}}</option>
			</b-select> 
			
			<b-form-group label="Stepper Timings:" v-show="drive.stepperDriver=='Custom'">
				<timing-input :drive="drive" :presetDrive="presetDrive" :index="index"/>
			</b-form-group>
		</td>
		<td>
			<steps-per-mm-input :drive="drive" :presetDrive="presetDrive" :index="index" />
		</td>
		<td>
			<b-form-input v-model.number="drive.instant_dv" v-preset="presetDrive.instant_dv" title="Maximum allowed instantenous speed change (M566)" min="0.1" :max="drive.max_speed" type="number" step="any" required />
		</td>
		<td>
			<b-form-input v-model.number="drive.max_speed" v-preset="presetDrive.max_speed" title="Maximum allowed speed (M203)" :min="drive.instant_dv" type="number" step="any" required />
		</td>
		<td>
			<b-form-input v-model.number="drive.acceleration" v-preset="presetDrive.acceleration" title="Drive acceleration (M201)" min="1" type="number" step="any" required />
		</td>
		<td>
			<b-form-input v-model.number="drive.current" v-preset="presetDrive.current" title="Motor current (M906)" min="300" :max="board.motorLimitCurrent" type="number" step="any" required />
				<span v-if="drive.current >= board.motorWarningCurrent" v-b-tooltip.hover title="Your specified motor current exceeds the safe range. You may have to use extra cooling to prevent damage of your stepper drivers!" class="text-warning small-text"><font-awesome-icon icon="exclamation-triangle" /> <u class="ml-1">Warning!</u></span>
		</td>
		<td>
			<b-select v-model.number="drive.driver" :options="drivers" v-preset="index" title="Motor driver (M584)" />
		</td>
	</tr>
</template>

<script>
'use strict';

import MotorsStepsPerMmInput from './MotorsStepsPerMmInput.vue'
import StepperTimingInput from '../components/StepperTimingInput.vue'

export default {

	beforeMount(){
		if(this.drive.stepperDriver == null) {
			this.drive.stepperDriver = ((this.template.lpc.stepperDriver != null) ? this.template.lpc.stepperDriver : "Custom")
		}
		this.drive.stepperDriverTimings = this.stepperDriverTimings[this.drive.stepperDriver]; //set the timings
	},
	data() {
		return {
			stepperDriverTimings: {
				"DRV8825": "1.9:1.9:0.65:0.65",
				"A4988": "1.0:1.0:0.2:0.2",
				"A4982": "1.0:1.0:0.2:0.2",
				"A5984": "1.0:1.0:0.4:0.4",
				"TMC220x": "0.1:0.1:0.02:0.02",
				"TMC222x": "0.1:0.1:0.02:0.02",
				"THB6128": "0.5:0.5:0.5:0.5",
				"Custom": "1.0:1.0:0.4:0.4",
			},
		}
	},

	components: {
		'steps-per-mm-input': MotorsStepsPerMmInput,
		'timing-input': StepperTimingInput
	},
	computed: {
		driverOption: {
			get() {
				return this.drive.stepperDriver; 
			},
			set(value) {					
				//console.log("Set DriverOption: " + value);
				this.drive.stepperDriver = value;

				if(value != "Custom"){
					this.drive.stepperDriverTimings = this.stepperDriverTimings[value];
					//console.log(this.stepperDriverTimings[value]);
				}
			}
		}


	},
	methods: {
		getDriveCaption(drive) {
			switch (drive) {
				case 0: return "X";
				case 1: return "Y";
				case 2: return "Z";
				default: return "E" + (drive - 3);
			}
		}
	},
	props: {
		index: Number,
		drive: Object,
		presetDrive: Object,
		drivers: Array
	}
}
</script>
