import {smoothieboard} from "./Smoothieboard.js"
import {rearm} from "./Rearm.js"
import {mkssbase1_3} from "./MKSSBase1.3.js"
import {biquskr1_3} from "./SKR1.3.js"


export default {
	getBoards: () => [
        smoothieboard,
		rearm,
		mkssbase1_3,
		biquskr1_3,
    ],

    
    
	getBoard(boardType) {			
		const boardDefinitions = this.getBoards();
		for(let i in boardDefinitions) {
			if (boardDefinitions[i].name == boardType) {
				return boardDefinitions[i];
			}
		}
		throw "Invalid board";
	}
}
