<style scoped>

.output_pin {
	min-width: 8.5rem;
}

</style>


<template>
	<b-container>
		<b-card no-body>
		<b-alert show v-if="freeMosfets.length > 0">
			<span><strong>Hint: </strong>Part cooling fans should not enable thermostatic control. Thermostatic control monitors the temp of the heaters and turns on when over the trigger point, and off when under the trigger point.</span>
		</b-alert>
		<b-alert show v-if="freeMosfets.length == 0">
			<span>There are no more free mosfests to use for part cooling or thermotatic control.</span>
		</b-alert>


			<template slot="header" >
				<span class="mt-2">Cooling Fans</span>
				<b-button-group class="float-right">
					<b-button size="sm" variant="success" :disabled="!canAddFan" @click="addFan"><font-awesome-icon icon="plus" /> Add Fan</b-button>
					<b-button size="sm" variant="danger" :disabled="!canRemoveFan" @click="removeFan"><font-awesome-icon icon="minus" /> Remove Fan</b-button>
				</b-button-group>
			</template>

			<table class="table mb-0" v-if="freeMosfets.length > 0">
				<thead>
					<th>Name</th>
					<th>Speed</th>
					<th>Inverted</th>
					<th>Frequency</th>
					<th>Thermostatic Control</th>
					<th>Monitored Heaters</th>
					<th>Thermostatic Mode Trigger Temperature</th>
					<th class="output_pin">Output Pin</th>
				</thead>
				<tbody>
					<tr v-for="(fan, index) in template.fans" :key="index">
						<td class="pt-3">FAN{{ index }}</td>
						<td>
							<b-input-group append="%">
								<b-form-input v-model.number="fan.value" v-preset="getFanPreset(index).value" title="Initial value of this fan when turned on" min="0" max="100" type="number" step="any" required />
							</b-input-group>
						</td>
						<td>
							<b-form-radio-group buttons button-variant="outline-primary" v-model="fan.inverted" v-preset="getFanPreset(index).inverted" title="Invert fan PWM signal (required for 4-pin PWM cooling fans)" :name="'fan-inverted-' + index">
								<b-form-radio :value="true">Yes</b-form-radio>
								<b-form-radio :value="false">No</b-form-radio>
							</b-form-radio-group>
						</td>
						<td>
							<b-input-group append="Hz">
								<b-form-input v-model.number="fan.frequency" v-preset="getFanPreset(index).frequency" title="PWM frequency of this fan. On LPC boards, this should be generally be set to 250Hz to match the hotend PWM frequency" type="number" min="0" max="65535" step="any" required />
							</b-input-group>
						</td>
						<td>
							<b-form-radio-group buttons button-variant="outline-primary" v-model="fan.thermostatic" v-preset="getFanPreset(index).thermostatic" title="Enable thermostatic control for this fan" :name="'fan-thermostatic-' + index">
								<b-form-radio :value="true">Yes</b-form-radio>
								<b-form-radio :value="false">No</b-form-radio>
							</b-form-radio-group>
						</td>
						<td>
							<b-form-checkbox-group buttons button-variant="outline-primary" v-model="fan.heaters" v-preset="getFanPreset(index).heaters" title="Heaters to monitor for thermostatic control" :disabled="!fan.thermostatic" :name="'fan-heaters-' + index" :options="heaters" />
							<span v-if="heaters.length == 0" class="text-muted">n/a</span>
						</td>
						<td>
							<b-input-group append="C">
								<b-form-input v-model.number="fan.trigger_temperature" v-preset="getFanPreset(index).trigger_temperature" title="Temperature at which the thermostatically-controlled fan is activated" :disabled="!fan.thermostatic" type="number" min="-273" max="1999" step="any" required />
							</b-input-group>
						</td>
						<td >
							<b-select v-model="fan.fanPin">
								<option v-for="channel in freeMosfets" v-bind:value="channel.value">{{channel.text}}</option>
							</b-select> 

						</td>
					</tr>
				</tbody>
			</table>
		</b-card>

		<b-card header="Leds" class="mt-3">
			<b-form-group label="Diagnostic LED:">
				<b-select v-model="template.lpc.diagnosticPin" v-preset title="LED to use for Diagnostics">
					<option v-for="(item, index) in this.board.lpc.LEDs" :value="item.value" v-text="item.text" />
					<option value="NoPin">No LED</option>
				</b-select>

			</b-form-group>
		</b-card>

		<b-card header="LCD" class="mt-3">
			<span>Enable LCD (ST9720 LCD only):   </span>
			<b-form-radio-group buttons button-variant="outline-primary" v-preset v-model="template.lpc.lcdEnabled" title="Enable LCD Support">
				<b-form-radio :value="true">Yes</b-form-radio>
				<b-form-radio :value="false">No</b-form-radio>
			</b-form-radio-group>

			<table class="table mb-0" v-if="template.lpc.lcdEnabled">
				<thead>
					<th>CS Pin</th>
					<th>Beep Pin</th>
