					<th>EncoderB Pin</th>
					<th>EncoderSW Pin</th>
					<th>DC Pin</th>
					<th>Panel Button Pin</th>
				</thead>
				<tbody>
					<tr>
						<td><b-form-input v-model="template.lpc.lcd.lcdCSPin" v-preset title="LCD Chip Select Pin"/></td>
						<td><b-form-input v-model="template.lpc.lcd.lcdBeepPin" v-preset title="LCD Beep Pin"/></td>
						<td><b-form-input v-model="template.lpc.lcd.encoderPinA" v-preset title="LCD Encoder A Pin"/></td>
						<td><b-form-input v-model="template.lpc.lcd.encoderPinB" v-preset title="LCD Encoder B Pin"/></td>
						<td><b-form-input v-model="template.lpc.lcd.encoderPinSw" v-preset title="LCD Encoder Switch Pin"/></td>
						<td><b-form-input v-model="template.lpc.lcd.lcdDCPin" v-preset title="LCD DC Pin"/></td>
						<td><b-form-input v-model="template.lpc.lcd.panelButtonPin" v-preset title="LCD Panel Button Pin"/></td>						
					</tr>
				</tbody>
			</table>
		</b-card>

		<b-card header="SD Cards" class="mt-3">

			<b-form-group>
				<span>Internal SDCard SPI Frequency:   </span>
				<b-form-input v-model="template.lpc.internalSDCardSPIFrequencyHz" v-preset title="Internal SDCard SPI Frequency (Hz)"/>
			</b-form-group>

			<span>Enable External SDCard:   </span>
			<b-form-radio-group buttons button-variant="outline-primary" v-model="template.lpc.externalSDCard.present" title="Enable External SDCard Support">
				<b-form-radio :value="true">Yes</b-form-radio>
				<b-form-radio :value="false">No</b-form-radio>
			</b-form-radio-group>



			<table class="table mb-0" v-if="template.lpc.externalSDCard.present">
				<thead>
					<th>External SD CS Pin</th>
					<th>External SD CD Pin </th>
					<th>External SPI Freqency</th>
				</thead>
				<tbody>
					<tr>
						<td><b-form-input v-model="template.lpc.externalSDCard.csPin" v-preset title="External SDCard Chip Select Pin"/></td>
						<td><b-form-input v-model="template.lpc.externalSDCard.cardDetectPin" v-preset title="External SDCard Card Detect Pin"/></td>
						<td><b-form-input v-model="template.lpc.externalSDCard.spiFrequencyHz" v-preset title="External SDCard SPI Frequency (Hz)"/></td>
					</tr>
				</tbody>
			</table>
		</b-card>

		<b-card header="Miscellaneous" class="mt-3">
			<label for="custom_settings">Custom Settings for config.g:</label>
			<label class="float-right"><a href="https://duet3d.com/wiki/G-code" target="_blank">Full list of all available G-codes</a></label>
			<b-form-textarea id="custom_settings" v-model="template.custom_settings" rows="4" max-rows="8" />
		</b-card>


	</b-container>
</template>

<script>
export default {
	beforeMount() {

		//build an array of Free Mosfets we could use for Fans.
		this.board.lpc.mosfets.forEach(function(mosfet, index) {
			let taken = false;
			for(var i=0; i<this.template.heaters.length; i++){
				if(mosfet.value == this.template.heaters[i].heater){
					if(i == 0 && !this.template.bed.present){
						//heatbed not present, keep searching
					} else {
						taken = true;
						break;
					}
				} 
			}

			if(taken == false) this.freeMosfets.push({text: mosfet.text, value: mosfet.value});

		}, this);


		//rearm may have no free mosfets for fans if 2 extruders are used... clean up the fans
		while(this.template.fans.length > this.freeMosfets.length){
			this.removeFan(); //remove ununsed fans
		}

		//check if we have template values for fans, if not populate with default fan pins from lpc defualts
		if(this.template.fans.length >0 && this.template.fans[0].fanPin == -1){

			for(var i=0; i<this.template.fans.length; i++){
				
				if(i < this.template.lpc.fanPins.length){
					this.template.fans[i].fanPin = this.template.lpc.fanPins[i];					
				} 
			}
		}	

		//populate the presets for lpc defaults
		for(var i=0; i<this.preset.fans.length; i++){
				
				if(i < this.preset.lpc.fanPins.length){
					this.preset.fans[i].fanPin = this.template.lpc.fanPins[i];					
				} 
			}


		//heaters available for monitoring
		this.template.heaters.forEach(function(heater, index) {
			if (heater != null) {
				this.heaters.push({ text: (index == 0) ? "Bed" : `E${index - 1}`, value: index });
			}
		}, this);
	},
	computed: {
		canAddFan() {
			//return this.template.fans.length < this.board.maxFans;
			return this.template.fans.length < this.freeMosfets.length;
		},
		canRemoveFan() {
			return this.template.fans.length > 0;
		},

	},
	data() {
		return {
			heaters: [],
			freeMosfets: [],
		}
	},
	methods: {
		getFanPreset(index) {
			return (index < this.preset.fans.length) ? this.preset.fans[index] : this.preset.fans[this.preset.fans.length - 1];
		},
	}

	

}
</script>